import { prefix } from "../../../constants";
import { RichText, InnerBlocks, useBlockProps } from "@wordpress/block-editor";
const save = (props) => {
	const { attributes } = props;

	const { title = "Title", block_id, tag = "h3" } = attributes;

	const blockProps = useBlockProps.save({
		'id': block_id,
		'itemscope': true,
		itemprop: "mainEntity",
		itemtype: "http://schema.org/Question"
	});

	return (
		<div {...blockProps}>
			<div className={`${prefix}-accordion-item`}>
				<div className={`${prefix}-accordion-item-header`}>
					<RichText.Content
						tagName={tag}
						itemprop="name"
						value={`<a href="#">${title}</a>`}
					/>
				</div>
				<div
					className={`${prefix}-accordion-content-wrapper`}
					itemprop="acceptedAnswer"
					itemscope
					itemtype="http://schema.org/Answer"
				>
					<div className={`${prefix}-accordion-content`} itemprop="text">
						<InnerBlocks.Content />
					</div>
				</div>
			</div>
		</div>
	);
};
export default save;
