import { __ } from "@wordpress/i18n";
import { useEffect } from "@wordpress/element";
import { withInstanceId } from "@wordpress/compose";
import { RichText, InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { prefix } from "../../../constants";
import { getTypoStyle, getDimensionStyle } from "../../../helpers";

const Edit = (props) => {
	const { attributes, setAttributes, instanceId, clientId } = props;

	const {
		tag,
		headingTypo,
		contentTypo,
		headingPadding,
		contentPadding,
		headingBackground,
		headingColor,
		title,
	} = attributes;

	useEffect(() => {
		setAttributes({
			block_id: `${prefix}-accordion-item-block-instance-${instanceId}-${clientId}`,
		});
	}, []);

	const headingStyle = {
		...getTypoStyle(headingTypo),
		...getDimensionStyle("padding", headingPadding),
		background: headingBackground,
		color: headingColor,
	};

	const contentStyle = {
		...getTypoStyle(contentTypo),
		...getDimensionStyle("padding", contentPadding),
		display: "none",
	};

	const blockProps = useBlockProps();

	const toggle = (e) => {
		e.preventDefault();
		const target = e.target;
		const next = target.nextElementSibling;
		next.style.display = next.style.display === "none" ? "block" : "none";
		target.classList.toggle("content-hidden");

		if (target.classList.contains("content-hidden")) {
			target.innerHTML = "show";
		} else {
			target.innerHTML = "hide";
		}
	};

	return (
		<div {...blockProps}>
			<RichText
				tagName={tag}
				value={title}
				style={headingStyle}
				className={`${prefix}-accordion-title`}
				onChange={(title) => {
					setAttributes({ title });
				}}
				allowedFormats={["bold", "italic", "text-color"]}
				placeholder={__("Write a title", "rise-blocks")}
			/>

			<button className="content-hidden" onClick={toggle}>
				show
			</button>

			<div className={`${prefix}-accordion-content`} style={contentStyle}>
				<InnerBlocks />
			</div>
		</div>
	);
};

export default withInstanceId(Edit);
